
import ScrollView from '../../scroll-view/scroll-view';
import { isNotInUni } from '../../common/utils/utils';
import { setCustomElements } from '../../common/vue3/adapter.js';


let componentConfig = {};

if (isNotInUni()) {
  componentConfig = {
    ScrollView,
  };
  setCustomElements([
    'uni-scroll-view',
  ], app);
}


export const ScrollViewPureMixin = {
  components: {
    ...componentConfig,
  },
};
